import React from 'react';
import './App.css';



class WatchList extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {

    const{
      kitedata,
      symbolList,
      kitePerfData,
      onTableRowclick,
      tableId
    }=this.props;

    let tempSymbols=[];
    if(kitedata){
       tempSymbols = symbolList.filter(function(symbol){ return (symbol == 'SEPERATOR' || kitedata.ticks[symbol]!=undefined) });
    }

     
    return (
          <table>
            <thead>
              <tr>
                <th>Name</th>
                <th>Change</th>
                <th>LTP</th>
              </tr>
            </thead>

            <tbody>
            
             { tempSymbols.map((symbol,index)=> <WatchListRow tableId={tableId} onTableRowclick={onTableRowclick} key={symbol+index} kitedata={kitedata} kitePerfData={kitePerfData} symbol={symbol} />)}
             
            </tbody>


        </table>
    )
  }

}


export default WatchList;

class WatchListRow extends React.Component {
  constructor(props) {
    super(props);
  }



  openStockInfo=()=>{
    this.props.onTableRowclick(this.props.symbol,this.props.tableId);
  }


  render() {

    const{
      kitedata,
      symbol,
      kitePerfData,
      onTableRowclick,
      tableId
    }=this.props;

    let isSeperator = (symbol =='SEPERATOR')?true:false;

    let changeClass  = '';
    if( kitedata.ticks[symbol] ){
      changeClass=kitedata.ticks[symbol].change<0 ?'red':'green';
    }
    
    let prefStyle = {};
    if(kitePerfData && kitePerfData[symbol]){
      prefStyle.backgroundColor=kitePerfData[symbol].color;
    }


    return (
      <tr onClick={this.openStockInfo}>
        {!isSeperator &&   <td style={prefStyle}>{kitedata.ticks[symbol].name}</td> }
        {!isSeperator &&   <td className={changeClass} >{(kitedata.ticks[symbol].change).toFixed(2)}</td> }
        {!isSeperator &&   <td>{kitedata.ticks[symbol].lastTradedPrice}</td> }              
       </tr>
    );


  }

}